h1, h2, h3, h4, h5, h6, p, a, button, ul {
  margin: 0;
  padding: 0;
}

ul, li, a {
  color: inherit;
  line-height: 0;
  text-decoration: none;
  list-style: none;
}

button {
  all: unset;
  cursor: pointer;
  display: inline-block;
}

.backdrop {
  opacity: 1;
  background-color: #e3e0e0e6;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  padding-left: 120px;
  padding-right: 120px;
  transition-duration: .25s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden.backdrop {
  pointer-events: none;
  opacity: 0;
  transition-duration: .5s;
}

.modal-gift {
  background-color: #fff;
  border-radius: 25px;
  max-width: 450px;
  padding: 15px;
}

.backdrop.is-hidden .modal-gift {
  animation-name: modalDisappearance;
  animation-duration: .5s;
  animation-timing-function: ease-in-out;
}

.backdrop:not(.is-hidden) .modal-gift {
  animation-name: modalAppearance;
  animation-duration: .5s;
  animation-timing-function: ease-in-out;
}

@keyframes modalDisappearance {
  0% {
    transform: translate(0%)scale3d(1, 1, 1);
  }

  100% {
    transform: translate(175%, 100%)scale3d(.1, .1, 1);
  }
}

@keyframes modalAppearance {
  0% {
    transform: translate(-175%, -100%)scale3d(.1, .1, 1);
  }

  100% {
    transform: translate(0%)scale3d(1, 1, 1);
  }
}

.gift-btn-clouse {
  background-color: #ec7171;
  border: 1px solid #520019;
  border-radius: 5px;
  margin-left: auto;
  padding: 5px 10px;
  font-size: 24px;
  display: block;
}

.gift-btn-clouse:active {
  background-color: #ff0008;
