  scale: .9;
}

.txt-count-gifts {
  text-align: center;
  margin-top: 12px;
  font-size: 28px;
  font-weight: 700;
}

.swiper {
  margin-top: 30px;
}

.swiper-slide.gift-slide {
  border-radius: 15px;
  width: 420px;
  height: auto;
}

.gift-image {
  width: 420px;
  height: auto;
}

body {
  background-color: #ffebcd;
  padding-left: 120px;
  padding-right: 120px;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

nav {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

nav h1 {
  text-align: center;
  color: #915200;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
  font-size: 24px;
}

.greet-field {
  color: #7c761c;
}

.main-tittle {
  text-align: center;
  font-size: 24px;
}

.form-play-tittle {
  text-align: center;
  margin-top: 10px;
  font-size: 20px;
  display: block;
}

.form-play-tittle.not-active {
  display: none;
}

.tittle-points {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 24px;
}

.txt-about-gift {
  text-align: center;
  margin-top: 10px;
  font-size: 22px;
  font-weight: 700;
}

.game-section {
  background-color: #fff;
  border: 3px solid #000;
  border-radius: 25px;
  margin-top: 15px;
  padding: 15px;
}

.form-game-start {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 12px;
  margin-top: 15px;
  font-size: 30px;
  display: flex;
}

.form-game-start.not-active {
  opacity: 0;
  display: none;
}

.form-game-start input {
  border: 3px solid #083100;
  border-radius: 5px;
  outline: none;
  padding: 4px;
  font-size: 24px;
}

.form-game-start input::placeholder {
  color: #9d9a9a;
}

.form-game-start button {
  background-color: #96ec71;
  border: 1px solid #083100;
  border-radius: 5px;
  padding: 5px 10px;
}

.form-game-start button:active {
  background-color: #1bb300;
  margin-top: 3px;
  scale: .9;
}

.form-play {
  flex-wrap: wrap;
  gap: 14px;
  font-size: 22px;
  display: flex;
}

.form-play.not-active {
  opacity: 0;
  display: none;
}

.btn-number {
  background-color: #8d8888;
  border: 1px solid #000;
  border-radius: 8px;
  padding: 2px 10px;
}

.click-btn-number-false {
  animation-name: changeColorBtnFalse;
  animation-duration: .5s;
}

@keyframes changeColorBtnFalse {
  0% {
    transform: scale(1);
  }

  50% {
    background-color: #8d8888;
    transform: scale(2);
  }

  100% {
    background-color: #ff0008;
    transform: scale(2.2);
  }
}

.click-btn-number-true {
  animation-name: changeColorBtnTrue;
  animation-duration: 1s;
}

@keyframes changeColorBtnTrue {
  0% {
    transform: scale(1);
  }

  50% {
    background-color: #8d8888;
    transform: scale(2);
  }

  100% {
    background-color: #1bb300;
    transform: scale(2.2);
  }
}

.click-btn-number-red {
  background-color: #ff0008;
}

.click-btn-number-green {
  background-color: #1bb300;
}

.play-again-btn {
  background-color: #96ec71;
  border: 1px solid #000;
  border-radius: 8px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  padding: 3px 10px;
  font-size: 22px;
  display: block;
}

.play-again-btn.not-active {
  opacity: 0;
  display: none;
}

.play-again-btn:active {
  background-color: #1bb300;
  margin-top: 18px;
  scale: .9;
}

.btn-get-a-gift {
  background-color: #96ec71;
  border: 2px solid #000;
  border-radius: 8px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  padding: 3px 10px;
  font-size: 22px;
  display: block;
}

.btn-get-a-gift:disabled {
  background-color: #8d8888;
}

.btn-get-a-gift:disabled:active {
  background-color: #8d8888;
  margin-top: 15px;
  scale: 1;
}

.btn-get-a-gift:active {
  background-color: #1bb300;
  margin-top: 18px;
  scale: .9;
}

.btn-get-a-gift.not-active {
  opacity: 0;
  display: none;
}

.btnExit {
  background-color: #ec7171;
  border: 1px solid #520019;
  border-radius: 5px;
  margin-top: 35px;
  margin-left: auto;
  margin-right: auto;
  padding: 5px 10px;
  font-size: 24px;
  display: block;
}

.btnExit:active {
  background-color: #ff0008;
  margin-top: 38px;
  scale: .9;
}

@media screen and (width <= 767px) {
  body {
    padding-left: 12px;
    padding-right: 12px;
  }

  nav h1, .main-tittle {
    font-size: 14px;
  }

  .form-play-tittle {
    font-size: 10px;
  }

  .tittle-points {
    font-size: 14px;
  }

  .txt-about-gift, .form-game-start {
    font-size: 12px;
  }

  .form-game-start input {
    border: 1px solid #083100;
    padding: 2px;
    font-size: 12px;
  }

  .form-play {
    gap: 6px;
    font-size: 12px;
  }

  .play-again-btn, .btn-get-a-gift {
    font-size: 12px;
  }

  .btnExit {
    margin-top: 15px;
    padding: 3px 6px;
    font-size: 14px;
  }

  .btnExit:active {
    margin-top: 18px;
  }
}
